.cart-container {
    max-width: 1200px;
    margin: 40px auto;
    padding: 0 20px;
}

.cart-header {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 30px;
    padding-bottom: 10px;
    border-bottom: 2px solid #333;
    color: #333;
}

/* ====================================
 * 2. ì¥ë°”êµ¬ë‹ˆ í•­ëª© ëª©ë¡ (Cart Items)
 * ==================================== */
.cart-items {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 40px;
}

/* ê°œë³„ ì¥ë°”êµ¬ë‹ˆ í•­ëª© ìŠ¤íƒ€ì¼ */
.cart-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 0;
    border-bottom: 1px solid #eee;
}

/* ìƒí’ˆ ì •ë³´ (ì´ë¯¸ì§€, ì´ë¦„, ì˜µì…˜) */
.product-info {
    display: flex;
    align-items: center;
    flex: 2; /* í° ê³µê°„ì„ ì°¨ì§€ */
    min-width: 250px;
}

.cart-item .product-image {
    width: 90px;
    height: 90px;
    object-fit: cover;
    margin-right: 15px;
    border-radius: 4px;
    border: 1px solid #ddd;
}

.details {
    display: flex;
    flex-direction: column;
}

.product-name {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 5px;
}

.product-option {
    font-size: 13px;
    color: #666;
}

/* ìˆ˜ëŸ‰ ì¡°ì ˆ */
.quantity-control {
    display: flex;
    align-items: center;
    flex: 1;
    max-width: 120px;
    justify-content: center;
}

.quantity-input {
    width: 40px;
    text-align: center;
    border: 1px solid #ccc;
    padding: 5px 0;
    margin: 0 -1px; /* ë²„íŠ¼ê³¼ ì—°ê²°ë˜ë„ë¡ ê²½ê³„ì„  ì œê±° */
    background-color: #fff;
    font-size: 14px;
}

.quantity-btn {
    width: 70px;
    height: 47px;
    background-color: #f8f8f8;
    border: 1px solid #ccc;
    cursor: pointer;
    font-size: 1.5rem;
    line-height: 1;
    transition: background-color 0.2s;
}

.quantity-btn:hover:not(:disabled) {
    background-color: #e0e0e0;
}

.quantity-btn:first-child {
    border-radius: 4px 0 0 4px;
}

.quantity-btn:last-child {
    border-radius: 0 4px 4px 0;
}

.quantity-btn:disabled {
    cursor: not-allowed;
    color: #ccc;
}

/* ê°€ê²© í‘œì‹œ */
.price-row {
    flex: 1.5;
    text-align: right;
}

.price {
    font-size: 14px;
    color: #888;
    margin-bottom: 5px;
}

.item-total-price {
    font-size: 18px;
    font-weight: bold;
    color: #333;
}

/* ì‚­ì œ ë²„íŠ¼ */
.remove-btn {
    background: none;
    border: none;
    color: #dc3545;
    cursor: pointer;
    padding: 5px 10px;
    transition: opacity 0.2s;
    font-size: 14px;
    flex-shrink: 0;
}

.remove-btn:hover {
    opacity: 0.7;
}

/* ì¥ë°”êµ¬ë‹ˆê°€ ë¹„ì—ˆì„ ë•Œ */
.empty-cart {
    /* HTML ì½”ë“œì˜ tailwind í´ë˜ìŠ¤(text-center py-10 text-gray-500 text-lg border border-dashed border-gray-300 rounded-md)ë¥¼ 
       ì¼ë°˜ CSSë¡œ ëŒ€ì²´í•©ë‹ˆë‹¤. */
    text-align: center;
    padding: 40px 0;
    color: #999;
    font-size: 18px;
    border: 2px dashed #ddd;
    border-radius: 8px;
    margin-top: 20px;
}

/* ====================================
 * 3. ìš”ì•½ ë° ê²°ì œ ì„¹ì…˜
 * ==================================== */
.cart-summary-wrapper {
    display: flex;
    justify-content: flex-end; /* ì˜¤ë¥¸ìª½ìœ¼ë¡œ ë°°ì¹˜ */
    margin-top: 40px;
    padding-top: 20px;
    border-top: 1px solid #ddd;
}

.cart-summary {
    width: 100%;
    max-width: 350px; /* ìš”ì•½ ì„¹ì…˜ì˜ ìµœëŒ€ ë„ˆë¹„ */
    padding: 20px;
    background-color: #f8f8f8;
