}

.summary-line {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    font-size: 15px;
    color: #555;
}

.summary-total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    padding-top: 15px;
    border-top: 1px solid #ddd;
    font-size: 18px;
    font-weight: bold;
}

.final-total {
    color: #dc3545; /* ê°•ì¡° ìƒ‰ìƒ */
    font-size: 24px;
}

.checkout-btn {
    width: 100%;
    padding: 12px;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    margin-top: 20px;
    transition: background-color 0.2s;
}

.checkout-btn:hover {
    background-color: #555;
}

/* ====================================
 * 4. ê³„ì† ì‡¼í•‘í•˜ê¸° ë§í¬
 * ==================================== */
.continue-shopping {
    text-align: center;
    margin-top: 20px;
}

.continue-shopping button {
    background: none;
    border: 1px solid #ccc;
    padding: 8px 20px;
    border-radius: 4px;
    color: #666;
    cursor: pointer;
    text-decoration: none;
    transition: background-color 0.2s;
}

.continue-shopping button:hover {
    background-color: #f5f5f5;
    color: #333;
}




